import React from 'react';
import { Table }  from 'react-bootstrap';
import { connect } from 'react-redux';

import './Cart.scss';

function Cart(props) {
    

    return (
        <div>
            <Table  responsive="lg" >
                <thead>
                <tr>
                    <th>목록</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>수량 변경</th>
          
                </tr>
                </thead>
                <tbody>
                {props.store.map( (store,idx) => {
                    
                    return (
                        <tr key={idx}>
                            <td>{idx + 1}</td>
                            <td>{store.name}</td>
                            <td>{store.quan}</td>
                            <td><button onClick={() => { props.dispatch({type:'plus' , payload:store.id}) }}> + </button> <button onClick={() => { props.dispatch({type:'minus', payload:store.id}) }}> - </button></td>
                      
                        </tr>

                    )
                })}
               

                </tbody>
            </Table>
            {
                
                props.alert_open === true ? 
                (
                    <div className="my-alert">
                        <p>지금구매하면 신규할인 20% </p>
                        <button onClick={()=>{ props.dispatch({type:'close'})  }} >닫기</button>        
                    </div>
                ) : null
            }

        </div>
    ) 
}


function CartConn(state) {
    return{
        store  : state.reducer ,
        alert_open : state.reducer2
    }
}

export default connect(CartConn)(Cart);

