import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';

import { BrowserRouter , HashRouter } from 'react-router-dom';

import { Provider } from 'react-redux'
import { combineReducers, createStore } from 'redux';


import './index.css';

import Header from './common/Header';
import Home from './pages/Home';
import reportWebVitals from './reportWebVitals';




let basicState = [];

let cartAlertState = true;

function  reducer(state = basicState, action){
  if(action.type === 'addCart' ){
   
   

    //같은id 구매목록 합치기
    let vIndx = state.findIndex(x => x.id === Number(action.payload.id));
    
    if(vIndx >= 0){
      state[vIndx].quan += action.payload.quan;
      return state;
    }


      let vBasicState = [...state];
      vBasicState.push({
      id: action.payload.id
      ,name: action.payload.name
      ,quan: action.payload.quan
      });
    
    return vBasicState;
  }else if(action.type === 'plus'){
    let vBasicState = [...state];
    
    let vState = vBasicState.findIndex(x => x.id === Number(action.payload));
    vBasicState[vState].quan++;

    return vBasicState;
  }else if(action.type === 'minus') {

    let vBasicState = [...state];
    let vState = vBasicState.findIndex(x => x.id === Number(action.payload));

    
    if(vBasicState[vState].quan !== 0){
      vBasicState[vState].quan--;
    }

    return vBasicState;
  
  }else{
    return state;
  }

}

function reducer2(state = cartAlertState, action){
  if(action.type === 'close'){
    return !cartAlertState
  }else{
    return state;
  }
}

let store = createStore(combineReducers({reducer,reducer2}));


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store} >
        <Header />
        <Home />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

